<template>
  <div>
    <md-whiteframe class="header-custom" md-tag="md-toolbar" md-elevation="3">
      <div class="md-toolbar-container">
        <md-button @click.native="$refs.leftSidenav.toggle()" class="btn-sidebar">
          <md-tooltip md-direction="top">Menu</md-tooltip>
          <md-icon>menu</md-icon>
        </md-button>
        <h1 class="md-title">{{myInfo.name}}
          <span class="hide">Full Stack Web Developer</span>
        </h1>
        <span style="flex: 1;">
        </span>
        <md-speed-dial class="md-fab-bottom-right fab-custom" md-direction="left" :class="{'static-speed-dial': scrollButtonVisible}">
          <md-button class="md-fab animate-attention" md-fab-trigger>
            <md-icon md-icon-morph>close</md-icon>
            <md-icon>mail</md-icon>
            <md-tooltip md-direction="top">Contact Now</md-tooltip>
          </md-button>
          <md-button class="md-fab md-mini" @click.native="sendEmail">
            <md-icon>mail_outline</md-icon>
            <md-tooltip md-direction="top">by Email</md-tooltip>
          </md-button>
          <md-button class="md-fab md-mini" @click.native="goto ('Contact')">
            <md-icon>chat_bubble_outline</md-icon>
            <md-tooltip md-direction="top">by Contact Form</md-tooltip>
          </md-button>
        </md-speed-dial>
      </div>
    </md-whiteframe>
    <md-sidenav class="main-sidebar md-left md-fixed" md-swipeable ref="leftSidenav" id="leftSidenav">
      <md-whiteframe class="header-custom" md-tag="md-toolbar" md-elevation="0">
        <div class="md-toolbar-container"></div>
        <md-list class="sidebar-menu">
          <md-list-item class="custom" @click.native="gotoFromSideNav(section.heading)" v-for="section in sections" :key="section.heading">
            <md-icon class="md-accent">{{section.icon}}</md-icon>
            <span>{{section.heading}}</span>
            <md-ink-ripple />
          </md-list-item>
        </md-list>
      </md-whiteframe>
    </md-sidenav>
    <a ref="link_email" id="link_email" style="display: none; visibility: hidden;" :href="emailAddressString">Email Me</a>
    <transition name="slide-left" appear mode="out-in">
      <div class="btn-scroll-to-top" v-show="scrollButtonVisible">
        <md-button class="md-fab md-clean" @click.native="gotoTop">
          <md-icon class="md-accent">arrow_upward</md-icon>
        </md-button>
      </div>
    </transition>
  </div>
</template>
<script>
  import debounce from 'lodash.debounce'

  export default {
    data () {
      return {
        scrollButtonVisible: false
      }
    },
    computed: {
      emailAddressString () {
        return 'mailto:' + this.myInfo.email + '?Subject=Hey Muhammad Ubaid, Iâ€™d really like to talk to you about this great opportunity!'
      }
    },
    methods: {
      gotoFromSideNav (section) {
        this.$refs.leftSidenav.close()
        this.goto(section)
      }
    },
    created () {
      var _self = this
      document.body.onscroll = debounce(function () {
        _self.scrollButtonVisible = document.body.scrollTop > 180
      }, 200, {
        leading: false,
        trailing: true
      })
    }
  }
</script>
<style>
  .header-custom {
    margin-bottom: 25px;
  }
  
  .header-custom .md-toolbar-container {
    padding-top: 128px;
    height: 228px;
  }
  
  .header-custom .fab-custom {
    bottom: -24px;
  }
  
  .md-list-item.custom {
    cursor: pointer;
  }
  
  .btn-scroll-to-top {
    position: fixed;
    right: 16px;
    bottom: 76px;
    z-index: 5;
  }
  
  .btn-menu-custom {
    position: fixed;
    left: 16px;
    top: 16px;
    z-index: 5;
  }
  
  .md-speed-dial.md-direction-left [md-fab-trigger]~.md-button {
    margin-right: 32px;
  }
</style>



