<template>
  <div id="app">
    <section-header></section-header>
    <md-layout class="padded-container">
      <md-layout md-hide-small md-flex="15"></md-layout>
      <md-layout class="full-width-child">
        <section-about :id="getSectionId(sections.about.heading)"></section-about>
        <section-services :id="getSectionId(sections.services.heading)"></section-services>
        <section-skills :id="getSectionId(sections.skills.heading)"></section-skills>
        <!--<section-portfolio :id="getSectionId(sections.portfolio.heading)"></section-portfolio>-->
        <section-contact :id="getSectionId(sections.contact.heading)"></section-contact>
      </md-layout>
      <md-layout md-hide-small md-flex="15"></md-layout>
    </md-layout>
    <section-footer></section-footer>
  </div>
</template>

<script>
  import Header from 'components/layout/Header'
  import Footer from 'components/layout/Footer'
  import about from 'components/sections/About'
  import services from 'components/sections/Services'
  import portfolio from 'components/sections/Portfolio'
  import skills from 'components/sections/Skills'
  import contact from 'components/sections/Contact'

  export default {
    name: 'app',
    components: {
      'section-header': Header,
      'section-footer': Footer,
      'section-about': about,
      'section-skills': skills,
      'section-services': services,
      'section-portfolio': portfolio,
      'section-contact': contact
    },
    methods: {
      getSectionId (heading) {
        return 'section-' + heading.toLowerCase().replace(/ /g, '')
      }
    }
  }
</script>

<style>
  .full-width-child>div {
    width: 100%;
    clear: both;
    display: block;
  }
</style>



