<template>
	<div class="portfolio-item">
		<md-card md-with-hover>
			<md-card-header>
				<div class="md-title">{{item.title}}</div>
				<div class="md-subhead">{{item.tags}}</div>
			</md-card-header>
			<md-card-media>
				<img :src="src[0]" alt="portfolio-item-image">
			</md-card-media>
			<md-card-content>
				{{item.summary}}
			</md-card-content>
		</md-card>
	</div>
</template>
<script>
	export default {
	  props: ['src', 'item']
	}
</script>
<style>
	.portfolio-item {
		max-width: 420px;
		display: inline-block;
		padding: 10px;
	}
</style>



