import Vue from 'vue'
import { mapGetters } from 'vuex'

var mixins = {
  computed: {
    ...mapGetters(['myInfo', 'services', 'skills', 'sections', 'portfolio'])
  },
  methods: {
    sendEmail () {
      document.getElementById('link_email').click()
    },
    goto (section) {
      section = section.replace(/ /g, '')
      window.setTimeout(() => {
        let sectionId = 'section-' + section.toLowerCase()
        document.getElementById(sectionId).scrollIntoView({ behavior: 'smooth' })
        window.setTimeout(() => {
          if (section.toLowerCase() === 'contact') {
            document.getElementById('txt_email').focus()
          }
        }, 250)
      }, 250)
    },
    gotoTop () {
      window.scroll({ top: 0, left: 0, behavior: 'smooth' })
    }
  }
}

Vue.mixin(mixins)



