<template>
  <div class="full-width">
    <h2 class="md-headline">
      {{sections.portfolio.heading}}
      <br/>
      <span class="md-caption" v-html="sections.portfolio.subheading"></span>
    </h2>
    <portfolio-item v-for="item in portfolio" :src="imageSource(item.images)" :item="item" :key="item.title"></portfolio-item>
  </div>
</template>

<script>
  import portfolioItem from './sub/portfolio-item'

  export default {
    components: {
      'portfolio-item': portfolioItem
    },
    methods: {
      imageSource (src) {
        let srcArr = []
        for (var i = 0; i < src.length; i++) {
          srcArr.push(require('assets/img/backgrounds/' + src[i]))
        }
        return srcArr
      }
    }
  }
</script>

<style>
  
</style>



